body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

p, h2 {
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  pointer-events: none;
  height: 40vmin;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: 20s linear infinite App-logo-spin;
  }
}

.App-header {
  color: #fff;
  background-color: #282c34;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  display: flex;
}

.App-link {
  color: #61dafb;
}

.mymanager-widget-container {
  z-index: 1000;
  width: 360px;
  position: absolute;
  bottom: 32px;
  left: 15px;
}

.mymanager-widget-body {
  background-color: #fff;
  border: 1px solid #0003;
  border-radius: 9999px;
  justify-content: space-between;
  align-items: center;
  gap: 24px;
  margin-top: 16px;
  padding: 10px 16px;
  display: flex;
  box-shadow: 0 1px 3px #0000001a, 0 1px 2px -1px #0000001a;
}

.mymanager-mapbox {
  border-radius: 9999px;
  width: 70px;
  height: 70px;
}

.mymanager-mapbox > div > div {
  border-radius: 50%;
}

.mymanager-truncate {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0 !important;
}

.mymanager-text-content {
  flex-direction: column;
  flex: 1;
  gap: 8px;
  display: flex;
}

.mymanager-title {
  font-size: 16px;
  font-weight: 700;
}

.mymanager-event-content {
  font-size: 14px;
  font-weight: 600;
}

.mymanager-company {
  font-size: 12px;
  font-weight: 600;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

